@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@800&display=swap");

@media only screen and (min-width: 602px) {
  body {
    background-color: hsl(218, 23%, 16%);
  }

  .App {
    text-align: center;
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: center;
    margin-top: 10%;
  }

  .Container {
    background-color: hsl(217, 19%, 24%);
    width: 40%;
    font-family: "Manrope", sans-serif;
    border-radius: 10px;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
  }

  .AdviceNum {
    color: hsl(150, 100%, 66%);
    font-size: 16px;
    letter-spacing: 4px;
    margin-top: 7%;
  }

  .AdviceQuote {
    color: hsl(193, 38%, 86%);
    font-size: 28px;
    text-align: center;
    width: auto;

    margin: 0px 30px;
  }

  .Divider {
    width: 80%;
    height: 22px;
    margin-top: 10px;
    margin-bottom: 60px;
  }

  .Button {
    background-color: hsl(150, 100%, 66%);
    padding: 16px 16px;
    border-radius: 100%;
    position: relative;
    top: -28px;
  }

  .Button:hover {
    cursor: pointer;
    box-shadow: 0px 0px 30px 1px hsl(150, 100%, 66%);
  }
}

@media only screen and (max-width: 601px) {
  body {
    background-color: hsl(218, 23%, 16%);
  }

  .App {
    text-align: center;
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: center;
    margin-top: 10%;
  }

  .Container {
    background-color: hsl(217, 19%, 24%);
    border-radius: 10px;
    max-width: 95%;
    min-height: 40vh;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    align-items: center;
  }

  .AdviceNum {
    color: hsl(150, 100%, 66%);
    font-size: 14px;
    letter-spacing: 4px;
    margin-top: 10%;
  }

  .AdviceQuote {
    color: hsl(193, 38%, 86%);
    font-size: 28px;
    text-align: center;
    margin: 0px 5%;
  }

  .Divider {
    max-width: 90%;
    margin-bottom: 20%;
  }

  .Button {
    background-color: hsl(150, 100%, 66%);
    padding: 16px 16px;
    border-radius: 100%;
    position: relative;
    top: -28px;
  }
}

.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}
